/** Gradients */

body {
  background: #FFF;
}

img {
  max-width: 100%;
}

.wrapper {
  max-width: 1280px;
}

.row {
  margin-right: 0;
  margin-left: 0;
}

.col-xs-12 {
  padding: 0;
}

.content-wrapper {
  max-width: 900px;
  margin: 0 auto;
}

.parallax {
  position: relative;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}

.hidden {
  display: none;
}

.col-sm-12 {
  width: 100%;
}

abbr[title] {
  text-decoration: none;
  font-size: 10px;
  position: relative;
  bottom: 5px;
}

.post-template-default article {
  margin-top: 2em;
}

.vertical-padding {
  padding: 3em 0;
}

body {
  font-family: 'Merriweather', serif;
}

strong {
  font-weight: 500;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 45px;
  text-transform: uppercase;
  color: #FFF;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
}

h2,
.products .products-container article p.price {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 40px;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 35px;
  color: #FFF;
  text-transform: uppercase;
}

h4,
.map-container aside .state-name {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 30px;
  margin-bottom: 0.5em;
}

h5,
.sticky-cart dt {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 17px;
  text-transform: uppercase;
}

p {
  font-size: 17px;
  font-weight: 300;
  line-height: 2em;
  margin-bottom: 1.5em;
}

a {
  color: #5D7C94;
}

a:hover {
  color: #1B2F3F;
  text-decoration: underline;
}

.smaller-text {
  font-size: 12px;
  display: block;
  line-height: 1.2em;
  color: #858585;
}

ul li,
ol li {
  font-size: 17px;
  font-family: "Merriweather", serif;
  font-weight: 300;
  line-height: 2em;
}

/** Media alignment */

.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}

.aligncenter {
  display: block;
  margin: 1rem auto;
  height: auto;
}

.alignleft,
.alignright {
  margin-bottom: 1rem;
  height: auto;
}

@media (min-width: 30rem) {
  .alignleft {
    float: left;
    margin-right: 1rem;
  }

