  content: counter(progressStep);
  height: 1.5rem;
  left: 50%;
  margin-left: -0.75rem;
  line-height: 1.5rem;
  padding-left: 0.1rem;
  top: 0;
  transform: scale(1, 1);
  width: 1.5rem;
  z-index: 1;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
}

ol.form-progress li::after {
  content: '';
  height: 0.25rem;
  left: -50%;
  top: 0.65rem;
