  display: none;
}

.buynowform .row {
  padding: 1em 0;
}

.buynowform label {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 18px;
  display: block;
  color: #000;
}

.buynowform label.smaller-text {
  font-weight: 400;
  font-size: 0.9em;
}

.buynowform p {
  margin: 0 0 1em;
  font-size: 0.9em;
}

.buynowform input:not([type='checkbox']),
.buynowform textarea,
.buynowform select {
  background-color: white !important;
  border: 1px solid #b8b8b8;
  display: block;
  margin: 10px 0 20px 0;
  width: 100%;
  padding: 10px;
  font-size: 13px;
  box-sizing: border-box;
}

.buynowform input:not([type='checkbox']):focus,
.buynowform textarea:focus,
.buynowform select:focus {
  outline: none;
  border-bottom: 3px solid #3786B5;
}

.buynowform select {
  height: 38px !important;
  -webkit-border-radius: 0;
  border: 0;
  outline: 1px solid #858585 !important;
  outline-offset: -1px;
}

.buynowform input[type='checkbox'] {
  margin-right: 10px;
  float: left;
}

.buynowform .buttons {
  display: flex;
  justify-content: center;
}

.buynowform .buttons .btn,
.buynowform .buttons .btn-alt,
.buynowform .buttons #buy-now-drawer .buynow button,
#buy-now-drawer .buynow .buynowform .buttons button,
.buynowform .buttons .buynow button,
.buynow .buynowform .buttons button {
  margin: 1em 1em 3em 1em;
}

.buynowform input#confirmation + .help {
  display: none !important;
}

.buynowform #total-price {
  text-align: center;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-size: 1.5em;
  font-weight: 500;
  color: #3786B5;
}

.billing-address-same {
  display: none;
}

.valid {
  border-bottom: 3px solid #3786B5 !important;
}

input[aria-invalid='true'] {
  border-bottom: 3px solid #B90A0A !important;
}

div.help {
  color: #B90A0A;
  font-size: 10px;
  position: relative;
  bottom: 16px;
}

.tingle-modal-box h4,
.tingle-modal-box .map-container aside .state-name,
.map-container aside .tingle-modal-box .state-name {
  margin: 0;
}

.tingle-modal-box p,
.tingle-modal-box li {
  font-size: 0.9em;
  line-height: 1.5em;
}

.tingle-modal__close {
  font-size: 4rem;
}

.claims-sections {
  display: flex;
  flex-wrap: wrap;
}

.claims-sections article {
  flex-grow: 1;
  flex-basis: 0;
  background: #5D7C94;
  color: #EFEFEF;
  padding: 0 2em;
  margin: 1em 2em;
}

.claims-sections a {
  color: #1B2F3F;
}

.testimonials {
  text-align: center;
}

.testimonials hr:last-of-type {
  display: none;
}

.entry-title {
  font-size: 35px;
}

@media (max-width: 767px) {
  .header-frontpage h1 {
    width: auto;
  }

  .callout .callout-text {
    width: auto;
    left: 20px;
    margin: 0;
  }

  .products .products-container {
    width: auto;
    margin: 40px 20px;
  }

  .latestposts {
    padding: 0 20px;
  }

  .latestposts article {
    margin-bottom: 2em;
  }

  .latestposts article:nth-of-type(-n+2) {
    border-right: none;
    border-bottom: 1px solid #5D7C94;
    padding-bottom: 1em;
    margin-bottom: 1em;
  }

  .nav-trigger {
    display: block;
    position: relative;
    text-align: right;
    z-index: 20;
  }

  .nav {
    display: none;
    position: absolute;
    box-sizing: border-box;
    margin: 0;
    padding: 100px 0;
    right: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: #FFF;
    z-index: 10;
  }

  .nav li {
    margin-left: 0;
    display: block;
  }

  .nav li a {
    color: #000;
    text-shadow: none;
    display: block;
    padding: 20px;
  }

  .nav li a:hover {
    background: #000;
    color: #FFF;
    text-decoration: none;
  }

  #buynow {
    display: block;
    padding: 2em 10px;
  }

  #buynow .passes article {
    margin: 0;
  }

  #buynow .passes article:first-of-type {
    border-right: 2px solid #1B2F3F;
  }

  .buynow-desktop {
    display: none !important;
  }

  .buynow-mobile {
    display: block;
  }

  #map {
    display: none;
  }

  #coverage_select {
    display: block;
  }

  .claims-sections article {
    margin: 0;
  }

  button,
  input,
  optgroup,
  select,
  textarea {
    font-size: auto;
  }

  .wrapper {
    margin: 0 1em;
  }

  .skier-details .skier-container {
    position: relative;
    width: 70%;
  }

  .buynow {
    flex-direction: column;
  }

  .buynow .passes .avail,
  .buynow .passes .not-avail {
    width: auto;
  }

  .progress-step {
    font-size: 12px;
  }
}




